@import url(https://fonts.googleapis.com/css?family=Audiowide);
body{margin:0;font-family:'Roboto', sans-serif;box-sizing:border-box}

.input-container{display:flex;flex-direction:column;align-items:flex-start}.input-container .label{font-size:1rem;font-weight:300}.input-container .input{border:1px solid #b0bec5;border-radius:5px;background:#fff;padding:8px 5px}.input-container .input:focus{outline:none;border-color:#42A5F5}

.button{padding:7px 15px;border-radius:5px;border:1px solid #42A5F5;background-color:#42A5F5;color:#fff;min-width:120px;display:block;cursor:pointer;transition:background-color 0.1s ease;font-size:0.9rem}.button:hover,.button:active{background-color:#1976d2;border-color:#1976d2}.button:active{border:1px solid #37474F}.button.success{color:#fff;background-color:#66BB6A;border-color:#66BB6A}.button.success:hover{background-color:#4caf50;border-color:#4caf50}.button.success:active{border:1px solid #37474F}.button:focus{outline:none}.button:disabled{background-color:#b0bec5;border-color:#b0bec5;color:#f5f5f5;cursor:default}.button:disabled:hover{background-color:#b0bec5;border-color:#b0bec5}

.game-settings-title{font-size:1.3rem;font-weight:400}.game-settings .form .input-contianer{height:85px}.game-settings .form .input-contianer .error-text{color:#EF5350;font-size:0.8rem}

.sidebar{position:fixed;top:0;bottom:0;width:300px;overflow-y:auto;background-color:#eceff1;display:flex;flex-direction:column;justify-content:space-between;box-shadow:0px 0px 4px 0px rgba(0,0,0,0.25)}.sidebar .logo{display:flex;justify-content:center;align-items:center;min-height:100px;background-color:#37474F;color:#fff}.sidebar .logo img{width:60px}.sidebar .logo h1{font-weight:400;font-size:1.8rem;font-family:'Audiowide'}.sidebar .game-settings{height:100%}.sidebar .more-actions{height:200px;display:flex;flex-direction:column;justify-content:center;align-items:center;color:#37474F}.sidebar .more-actions .superman-container{height:100px;position:relative}.sidebar .more-actions .superman-container .superman-button{cursor:pointer;height:80%;transition:height 0.2s ease}.sidebar .more-actions .superman-container .is-superman-mode{position:absolute;top:35px;right:0;font-weight:500;font-size:1rem;color:#37474F}.sidebar .more-actions .superman-container .is-superman-mode-on{color:#4caf50}

.card{display:flex;flex-direction:column;align-items:center;width:150px}.card-content{font-size:2rem}.card-label{font-size:1rem;font-weight:400}


.score-panel{display:flex;justify-content:center;align-items:center}

.cell{border:1px solid #37474F;width:60px;height:60px;margin:2px;display:flex;justify-content:center;align-items:center;cursor:pointer;-webkit-user-select:none;-moz-user-select:none;-o-user-select:none;-ms-user-select:none;user-select:none}.cell:hover{background-color:#eceff1}.cell img{width:30px;height:50px}.cell.is-revealed{background-color:#f5f5f5;cursor:default}.cell .mined-neighbours{font-size:1.5rem}.cell .mined-neighbours-1{color:#009688}.cell .mined-neighbours-2{color:#8bc34a}.cell .mined-neighbours-3{color:#64b5f6}.cell .mined-neighbours-4{color:#1976d2}.cell .mined-neighbours-5{color:#ffa000}.cell .mined-neighbours-6{color:#ff7043}.cell .mined-neighbours-7{color:#f06292}.cell .mined-neighbours-8{color:#ef5350}

.board-wrapper{display:flex;overflow:auto;height:calc(100% - 100px)}.board-wrapper .board-row{display:flex}

.game-container{display:flex;flex-direction:column;height:100%}

.main{position:fixed;top:0;bottom:0;left:300px;right:0}.main .score-panel{height:100px}

